const slideMin = document.getElementById("slideMin");
const slideMax = document.getElementById("slideMax");
const minVal = document.querySelector(".min-val");
const maxVal = document.querySelector(".max-val");
const priceInputMin = document.querySelector(".min-input");
const priceInputMax = document.querySelector(".max-input");
const minTooltip = document.querySelector(".min-tooltip");
const maxTooltip = document.querySelector(".max-tooltip");
const minGap = 0;
const range = document.querySelector(".slider-track");
const sliderMinValue = parseInt(minVal.min);
const sliderMaxValue = parseInt(maxVal.max);
const progres = document.querySelector(".progres");

slideMin.oninput = () => {
  let gap = parseInt(maxVal.value) - parseInt(minVal.value);
  if (gap <= minGap) {
    minVal.value = parseInt(maxVal.value) - minGap;
  }
  minTooltip.innerHTML = minVal.value + " " + "$";
  priceInputMin.value = minVal.value;
  minTooltip.classList.add("show");
  setArea();
};

minVal.onblur = () => {
  minTooltip.classList.remove("show");
  maxTooltip.classList.remove("show");
};

slideMax.oninput = () => {
  let gap = parseInt(maxVal.value) - parseInt(minVal.value);
  if (gap <= minGap) {
    maxVal.value = parseInt(minVal.value) - minGap;
  }
  maxTooltip.innerHTML = maxVal.value + " " + "$";
  priceInputMax.value = maxVal.value;
  maxTooltip.classList.add("show");
  setArea();
};

function setArea() {
  range.style.left = `${
    ((minVal.value - sliderMinValue) / (sliderMaxValue - sliderMinValue)) * 100
  }%`;
  range.style.left = (minVal.value / sliderMaxValue) * 100 + "%";
  range.style.right = `${
    100 -
    ((maxVal.value - sliderMinValue) / (sliderMaxValue - sliderMinValue)) * 100
  }%`;

  minTooltip.style.left = (minVal.value / sliderMaxValue) * 100 + "%";
  maxTooltip.style.left = (maxVal.value / sliderMaxValue) * 100 + "%";
  progres.style.left = (minVal.value / sliderMaxValue) * 100 + "%";
  progres.style.right = 100 - (maxVal.value / sliderMaxValue) * 100 + "%";
}

let minPrice = (onchange = (e) => {
  e = parseInt(priceInputMin.value);
  if (e < sliderMinValue) {
    priceInputMin.value = sliderMinValue;
  }
  minVal.value = priceInputMin.value;
  slideMin();
});

let maxPrice = (onchange = (e) => {
  e = parseInt(priceInputMax.value);
  if (e > sliderMaxValue) {
    priceInputMax.value = sliderMaxValue;
  }
  maxVal.value = priceInputMax.value;
});
