document.addEventListener("DOMContentLoaded", function () {
  const menuButton = document.getElementById("menuButton");
  const dropdownMenu = document.getElementById("dropdownMenu");
  const svg = document.querySelector(".svg");

  const menuButton2 = document.getElementById("menuButton2");
  const dropdownMenu2 = document.getElementById("dropdownMenu2");
  const svg2 = document.querySelector(".svg2");

  const menuButton3 = document.getElementById("menuButton3");
  const dropdownMenu3 = document.getElementById("dropdownMenu3");
  const svg3 = document.querySelector(".svg3");

  const rangeButton = document.getElementById("rangeDropdown");
  const rangeDropdownBox = document.getElementById("rangeDropdownBox");

  rangeButton.addEventListener("click", function () {
    rangeDropdownBox.classList.toggle("active");
  });

  menuButton.addEventListener("click", function () {
    dropdownMenu.classList.toggle("active");
    svg.classList.toggle("opened");
  });

  menuButton2.addEventListener("click", function () {
    dropdownMenu2.classList.toggle("active");
    svg2.classList.toggle("opened");
  });

  menuButton3.addEventListener("click", function () {
    dropdownMenu3.classList.toggle("active");
    svg3.classList.toggle("opened");
  });
});
