const SidebarCheckBoxItem = document.querySelectorAll(
  ".sidebar__checkbox-item"
);
const SidebarCheckboxBox = document.querySelector(".breadcrumb");

const createChoiceItem = (text) => {
  return `
  <li class="breadcrumb__item">
    <span>${text}</span>
    <button class="breadcrumb__btn btn-reset">
      <img src="img/cancell.png" alt="" />
    </button>
  </li
  `;
};

SidebarCheckBoxItem.forEach((el) => {
  el.querySelector("input").addEventListener("change", (e) => {
    console.log(el.querySelector("input"));
    let checked = el.querySelector("input").checked;

    if (checked) {
      el.querySelector(".custom-checkbox").classList.add(
        "sidebar__checkbox--active"
      );
      let text = el.querySelector(".checkbox-text").textContent;

      SidebarCheckboxBox.insertAdjacentHTML(
        "afterbegin",
        createChoiceItem(text)
      );
    } else {
      el.querySelector(".custom-checkbox").classList.remove(
        "sidebar__checkbox--active"
      );
    }

    let activeCheckboxes = document.querySelectorAll(
      ".sidebar__checkbox--active"
    );

    if (activeCheckboxes.length > 0) {
      SidebarCheckboxBox.display = "block";
    } else {
      SidebarCheckboxBox.style.display = "none";
    }
  });
});

SidebarCheckboxBox.addEventListener("click", (e) => {
  if (e.target.classList.contains("breadcrumb__item")) {
    e.target.remove();

    let text = e.target.textContent.trimLeft().trimRight();
    console.log(text);

    document
      .querySelector(`[data-text='${text}']`)
      .querySelector("input").checked = false;
    document
      .querySelector(`[data-text='${text}']`)
      .classList.remove("sidebar__checkbox--active");
  }

  if ((e.currentTarget.children, length === 1)) {
    e.currentTarget.style.display = "none";
  }
});
